// 引入标题组件
import HeaderTitle from '@/components/common/PageNavBar/HeaderTitle';
// 引入标题元素组件
import HeadOperationItem from '@/components/common/PageNavBar/HeadOperationItem';
// 引入table组件
import VXETable from '@/components/common/Table';
// 引入提示组件
import { showSuccessMessage, showWarningMessage } from '@/utils/message';
import { showErrorNotify } from '@/utils/notify';
import {
  STATUS_CODE,
  TITLE,
  // MSG,
  VARIABLE,
  BTN_TEXT,
  CONSTANTS_PROCESS
} from '@/utils/constantsFile';
// 搜索组建
import SearchBox from '@/components/common/SearchBox';
import CustomColumn from '@/components/common/CustomColumn';
// 引入通用接口
import { deepClone } from '@/utils/publicFunction';
// 导入所需接口
import {
  reviewSelectListAction,
  configurationUpdateProcessConfigurationAction,
  userGetAllAction,
  reviewUpdateWorkflowByIdAction,
  reviewSelectInfoByIdAction
} from '@/api/processConfig';
export default {
  name: 'ProcessConfiguration',
  components: {
    // 加载标题组件
    HeaderTitle,
    // 加载标题元素组件
    HeadOperationItem,
    // 加载table组件
    VXETable,
    SearchBox,
    CustomColumn
  },
  data() {
    return {
      VARIABLE: VARIABLE,
      BTN_TEXT: BTN_TEXT,
      CONSTANTS_PROCESS: CONSTANTS_PROCESS,
      // 设备修改表单数据
      formUpdate: {},
      // 搜索框有关
      isSearchBoxShow: false,
      searchList: [],
      searchData: [],
      // 设备修改表单验证
      // 编码下拉
      codeOptions: [],
      // table多选数据
      dataChecks: [],
      tableName: 'device',
      // 用于操作当前显示哪个组件 0：主页面 1：新增子节点参数
      showPage: 0,
      // 表格查询表单数据
      formParams: {
        model: null
      },
      // 模态框名称
      titlename: '',
      // 控制模态框修改
      dialogVisibleUpdate: false,
      // table加载动画
      tableLoading: false,
      // table分页配置
      tablePage: {
        total: 0,
        currentPage: 1,
        pageSize: 10,
        pageSizes: [5, 10, 20, 50, 100, 200, 500],
        layouts: [
          'Sizes',
          'PrevJump',
          'PrevPage',
          'Number',
          'NextPage',
          'NextJump',
          'FullJump',
          'Total'
        ],
        perfect: true,
        slots: {
          // 使用自定义插槽
          left: 'pager_left'
        }
      },
      // table数据
      tableDatas: [],
      // table表头配置
      tableColumns: [
        {
          type: 'radio',
          align: 'center',
          visible: true,
          width: '50'
        },
        {
          field: 'reviewName',
          title: CONSTANTS_PROCESS.PROCESS_NAME,
          align: 'center'
        },
        {
          field: 'isGrab',
          title: CONSTANTS_PROCESS.IS_OPEN_ACCEPT,
          align: 'center',
          editRender: {
            name: '$select',
            options: [
              { label: VARIABLE.YES, value: true },
              { label: VARIABLE.NO, value: false }
            ],
            events: { change: this.sexChangeEvent }
          }
        },
        {
          field: 'isReview',
          title: CONSTANTS_PROCESS.IS_OPEN_AUDIT,
          align: 'center',
          editRender: {
            name: '$select',
            options: [
              { label: VARIABLE.YES, value: true },
              { label: VARIABLE.NO, value: false }
            ],
            events: { change: this.sexChangeEvent }
          }
        }
        // {
        //   field: '',
        //   title: BTN_TEXT.OPERATION,
        //   width: '100',
        //   align: 'center',
        //   slots: {
        //     default: ({ row, column }) => {
        //       return [
        //         <el-button
        //           onClick={() => this.showModal(row)}
        //           type="text"
        //         >
        //           {CONSTANTS_PROCESS.PROCESS}
        //         </el-button>
        //       ];
        //     }
        //   }
        // }
      ],
      // 控制自定义列模态框显示
      dialogVisibleCustomColumn: false,
      processAudit: 1, // 审核人数
      auditPersonOptions: [ // 审核人
      ],
      checkList: [],
      auditPersonArr: [], // 任务人员组数据
      dialogVisibleAuditUser: false,
      nowChoose: 0, // 当前选择
      reviewId: 0 // 审核id
    };
  },
  computed: {},
  created() {
    // 加载扩展字段
    // this.setTableColume();
    // 调用设备列表查询
    this.findList();
    // this.getSearchList();
  },
  watch: {
    // 切换模态框显示和隐藏
    dialogVisibleUpdate(val) {
      if (val === false) {
        this.$refs.updateForm.resetFields();
      }
    }
  },
  methods: {
    // 按钮触发事件
    buttonClickEvent(type) {
      // if (type === 'exit') {
      //   this.$emit('closePage');
      //   return;
      // }
      this[`${type}`]();
    },
    async setProcess() {
      var row = {};

      if (!this.$refs.xGrid.getRadioRecord()) {
        showWarningMessage('请选择要操作的数据！');
        return false;
      } else {
        row = this.$refs.xGrid.getRadioRecord();
      }
      this.auditPersonArr = [];
      this.reviewId = row.reviewId;
      this.dialogVisibleUpdate = true;

      this.auditPersonArr.push({
        personName: CONSTANTS_PROCESS.SELECT_AUDIT,
        ids: []
      });

      // 审核详情
      try {
        const res = await reviewSelectInfoByIdAction({
          configurationId: this.reviewId
        }, {});
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          if (res.rows.workflowList.length > 0) {
            for (var i = 0; i < res.rows.workflowList.length; i++) {
              if (res.rows.workflowList[i].userList.length > 0) {
                if (!this.auditPersonArr[i]) {
                  this.auditPersonArr.push({
                    personName: CONSTANTS_PROCESS.SELECT_AUDIT,
                    ids: []
                  });
                }
                var arrUser = res.rows.workflowList[i].userList;
                this.auditPersonArr[i].ids = [];
                var userName = [];
                for (var j = 0; j < arrUser.length; j++) {
                  this.auditPersonArr[i].ids.push(arrUser[j].userId);
                  if (arrUser[j].userName) {
                    userName.push(arrUser[j].userName);
                  }
                }
                this.auditPersonArr[i].personName = userName.join(',');
              }
            }
          }
        } else {
          // 提示异常信息
          // showWarningMessage(res.message);
        }
      } catch (error) {
        // showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 查询所属参数
     * @param val
     * @return: 无
     * @author: ZJM
     */
    async findList() {
      try {
        // 显示table加载动画
        this.tableLoading = true;

        const res = await reviewSelectListAction({}, {});
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // 将数据写入表单
          this.tableDatas = res.rows;
          this.tablePage.total = res.total;
          this.tableLoading = false;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    refreshTable() {
      this.findList();
    },
    /**
     * 新增审核人（2）
     */
    addAuditPerson() {
      this.auditPersonArr.push({
        personName: CONSTANTS_PROCESS.SELECT_AUDIT,
        ids: []
      });
    },
    /**
     * 关闭审核人（2）
     */
    closeAuditPerson() {
      this.auditPersonArr.splice(1, 1);
    },
    async showAuditModal(flag) {
      this.checkList = [];
      // this.auditPersonOptions = [];
      this.dialogVisibleAuditUser = true;
      this.nowChoose = flag;
      try {
        const res = await userGetAllAction({}, {});
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.auditPersonOptions = res.rows;
          this.checkList = this.auditPersonArr[flag].ids;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    submitAuditUser() {
      this.dialogVisibleAuditUser = false;
      if (this.checkList.length === 0) {
        this.auditPersonArr[this.nowChoose].personName = CONSTANTS_PROCESS.SELECT_AUDIT;
        this.auditPersonArr[this.nowChoose].ids = [];
      } else {
        var arr = [];
        for (var i = 0; i < this.auditPersonOptions.length; i++) {
          for (var j = 0; j < this.checkList.length; j++) {
            if (Number(this.checkList[j]) === this.auditPersonOptions[i].userId) {
              arr.push(this.auditPersonOptions[i].userName);
            }
          }
        }
        this.auditPersonArr[this.nowChoose].personName = arr.join(',');
        this.auditPersonArr[this.nowChoose].ids = deepClone(this.checkList);
      }
    },
    async sexChangeEvent({ row }) {
      var configurationRelationTypeEnum = '';
      var processConfiguration = '';
      if (row.reviewType === 1) {
        configurationRelationTypeEnum = 'REPAIR';
      } else {
        configurationRelationTypeEnum = 'MAINTAIN';
      }
      if (row.isGrab === true && row.isReview === true) {
        processConfiguration = '1-2-3-4';
      } else if (row.isGrab === false && row.isReview === true) {
        processConfiguration = '2-3-4';
      } else if (row.isGrab === false && row.isReview === false) {
        processConfiguration = '2-4';
      } else if (row.isGrab === true && row.isReview === false) {
        processConfiguration = '1-2-4';
      }
      try {
        const res = await configurationUpdateProcessConfigurationAction({
          configurationRelationTypeEnum: configurationRelationTypeEnum,
          processConfiguration: processConfiguration
        }, {});
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          showSuccessMessage(res.message);
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    async submitProcess() {
      try {
        var workflow = [];
        for (var i = 0; i < this.auditPersonArr.length; i++) {
          if (this.auditPersonArr[i].ids.length > 0) {
            var jsonArr = {
              userList: []
            };
            var arr = this.auditPersonArr[i].ids;
            for (var j = 0; j < arr.length; j++) {
              var json = {
                userId: arr[j]
              };
              jsonArr.userList.push(json);
            }
            workflow.push(JSON.stringify(jsonArr));
          }
        }
        var abc = '[' + workflow.join(',') + ']';
        const res = await reviewUpdateWorkflowByIdAction({
          reviewId: this.reviewId,
          workflow: abc
        });
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // 将数据写入表单
          showSuccessMessage(res.message);
          this.dialogVisibleUpdate = false;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * 显示审核文字
     * @param {*} index 下标
     */
    showNumIndex(index) {
      var str = '';
      switch (index) {
        case 0:
          str = '①';
          break;
        case 1:
          str = '②';
          break;
      }
      return str;
    }
  }
};
